// Our title is a bit long
.usa-header--basic .usa-navbar {
  @include at-media(desktop) {
    width: 40%;
  }
}

.chp-home-hero {
  $hero-min-height-sm: 400px;
  $hero-min-height-lg: 500px;
  background-color: #000;
  background-repeat: no-repeat;
  min-height: $hero-min-height-sm;
  background-image: url(../img/hero--home-sm.png);

  @include at-media(desktop) {
    background-position: right;
    background-image: url(../img/hero--home-lg.png);
    min-height: $hero-min-height-lg;
  }

  &__mask {
    width: 100%;
    min-height: $hero-min-height-sm;
    background: linear-gradient(to bottom, black, 60%, transparent);

    @include at-media(desktop) {
      min-height: $hero-min-height-lg;
      background: linear-gradient(to right, black, 77%, transparent);
    }
  }

  &__body {
    margin-left: auto;
    margin-right: auto;
    max-width: 64rem;
  }

  &__quote {
    border: 0;
    margin: 0;
    padding: units(4);
    color: color('white');
    @include u-font-family('serif');
    @include u-font-size('body', 'md');

    @include at-media(desktop) {
      @include u-font-size('body', '3xl');
      padding: units(7) 16rem units(4) units(4);
    }
  }

  &__quote-attribution {
    display: block;
    margin-top: units(1);
    font-style: italic;
    @include u-font-family('serif');
    @include u-font-size('body', 'md');

    @include at-media(desktop) {
      text-align: right;
      padding-right: 5rem;
    }
  }

  &__title {
    color: color('white');
    margin: 0;
    padding: units(2);

    @include at-media(desktop) {
      padding: units(6) units(4);
    }
  }

  &__caption {
    width: 100%;
    padding: units(1) 0;
    max-width: inherit;
    background-color: color('white');
    @include u-font-size('body', '2xs');
  }
}

.chp-home {
  &__lead {
    @include u-font-family('serif');
    @include u-font-size('body', 'md');
  }
}


.chp-home-certs {
  position: relative;
  text-align: center;
  @include u-padding-y(5);
  width: 100%;

  @include at-media('mobile-lg') {
    @include grid-col(6);
  }

  &__stat {
    @include u-font-size('body', '3xl');
  }

  &__desc {
    @include u-font-size('body', 'lg');
  }
}

.chp-home-certs::after {
  content: "";
  background-image: url(../img/solid/times.svg);
  background-size: 35%;
  background-repeat: no-repeat;
  background-position: center center;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  opacity: 0.3;
}

.chp-home {
  &__sub-title {
    text-transform: uppercase;
    letter-spacing: .3em;
    font-weight: 400;
  }
}

.chp-home-features {
  &__row {
    @include grid-row();
    @include grid-gap(3);
  }

  &__container {
    @include u-margin-y(2);

    @include at-media('tablet') {
      @include grid-col(6);
    }
    @include at-media('tablet-lg') {
      @include grid-col(4);
    }
  }
}

.chp-home-featured {
  display: block;
  height: 100%;
  box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px, rgba(0, 0, 0, 0.05) 0px 0.5px 1px;
  transition: all 0.333s ease-out 0s;
  border-radius: 4px;
  text-decoration: none;
  @include u-text('black');
  @include u-padding(3);

  &:visited {
    @include u-text('black');
  }

  &:hover {
    transform: translate3d(0px, -1px, 0px);
    box-shadow: rgba(0, 0, 0, 0.22) 0px 19px 43px, rgba(0, 0, 0, 0.18) 0px 4px 11px;
  }

  &__figure {
    flex-shrink: 0;
    margin: 0;
    @include u-padding-y(3);

    svg {
      width: 4rem;
      fill: color('primary');
    }

    &.is-astronaut svg {
      height: 4rem;
    }
  }

  &__body {
    flex: 1;
    min-width: 0;
    @include u-font-size('body', 'lg');
  }
}

.chp-banner--beta {
  background-color: color($theme-color-chp-blue-light);

  .usa-tag {
    margin-right: units(1);
  }

  &__body {
    @include grid-container;
    margin: 0 auto;
    @include u-padding(2);
  }

  &__text {
    margin: 0;
    @include u-font-size('body', 'md');
    max-width: inherit;
  }

  &__link {
    font-weight: bold;
    text-decoration: none;

    &:visited {
      color: color('blue-60v');
    }
  }
}

.chp-article.usa-section {
  @include at-media('desktop') {
    min-height: 70vh;
  }
}

.chp-article .chp-toolkit,
.chp-article .chp-highlight {
  @include u-margin-y(6);

  &__list {
    column-count: 2;
    column-gap: units(2);
  }
}

.chp-article .chp-toolkit {
  &__title {
    position: relative;
    color: color($theme-color-chp-blue-dark);
    text-transform: uppercase;

    &::before {
      content: '';
      position: absolute;
      height: 4px;
      top: -1rem;
      left: 0px;
      width: 100%;
      background-color: color($theme-color-chp-blue-dark);
    }
  }

  &__list {
    list-style: none;
    padding-left: 0;
  }

  &__item {
    margin-bottom: units(1);
  }

  &__link {
    svg {
      width: 22px;
      padding-right: units(1);
      fill: color('primary');
      vertical-align: middle;
    }
  }
}

.chp-article .chp-highlight {
  @include u-padding(2);
  background-color: color($theme-color-chp-blue-dark);
  border-radius: 3px;

  &__title,
  &__list,
  p,
  a {
    color: color('white');
  }
}

.chp-meta {
  padding: 0;

  @include at-media('desktop') {
    padding: 0;
  }

  &__title {
    margin-top: units(3);
    color: color($theme-color-chp-blue-dark);
    text-transform: uppercase;
  }

  &__desc {
    margin-top: units(1);
  }
}

.process-steps h3 {
  position: relative;
  padding-left: units(5);
}

.process-steps h3:before {
  content: attr(data-step);
  position: absolute;
  left: 0;
  top: 0;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  line-height: 30px;
  text-align: center;
  font-family: "Source Sans Pro","Helvetica Neue","Helvetica","Roboto","Arial",sans-serif;
  font-size: 100%;
  @include u-bg($theme-color-chp-green-dark);
  @include u-text('white');
}

.chp-quote {
  background-color: #f6f8fa;
  border-radius: 3px;
  font-size: 85%;
  line-height: 1.45;
  overflow: auto;
  @include u-padding(2);
  @include u-margin-y(2);
}

.chp-process {
  &__diagram {
    @include at-media('desktop') {
      margin-top: 30rem; // roughly aligns the process diagram with the relevant text
    }
  }
}

blockquote {
  border-left: .25em solid #dfe2e5;
  color: #6a737d;
  padding: 0 .5rem;
}

.chp-rolenav {
  @include u-margin-y(3);
}

.chp-phasenav,
.chp-rolenav {
  &__title {
    @include u-margin-y(1);
    color: color($theme-color-chp-blue-dark);
  }
}

.chp-article-sidenav__container {
  @include at-media('desktop') {
    position: -webkit-sticky;
    position: sticky;
    top: 5%;
  }
}

.chp-page-list {
  &__title {
    @include u-font-size('body', 'lg');
    font-weight: bold;
    margin: units(3) 0 units(2) 0;
  }

  &__desc {
    margin: 0 0 units(2) 0;
  }
}

.chp-page-list--process {
  &__title {
    @include u-font-size('heading', 'lg');

    a {
      @include permaLink();
    }
  }
}

.chp-agenda {
  th {
    width: 15%;
  }
  td {
    width: 85%;
  }
}

.chp-nested-list ul {
  margin: .5em 0 1em 0;
}

.chp-breadcrumb {
  border-bottom: 1px solid color('gray-cool-10');

  &__body {
    @include grid-container();
  }

  .chp-breadcrumb &__list {
    display: block;
    list-style-type: none;
    padding: 0;
    width: 100%;
    margin: units(2) auto;
  }

  &__item {
    display: inline-block;
    @include u-font-size('body', '2xs');
    margin: 0;
  }

  &__link {
    @include permaLink();
    text-decoration: none;
  }
}

.chp-checklist,
.usa-prose .chp-checklist {
  list-style-image: url('../img/bullet-sq.svg');

  li {
    margin: units(2) 0;
  }
}

.chp-footer {
  .usa-footer__primary-link {
    @include permaLink();
  }

  &__changelog-link {
    display: block;
    @include u-padding(2);
  }

  &__sub-list {
    list-style-type: none;
    padding-left: 0;
    width: 100%;

    @include at-media('mobile-lg') {
      @include grid-row;
    }

    li {
      @include at-media('mobile-lg') {
        @include grid-col(6);
      }
      @include at-media('tablet-lg') {
        @include grid-col(auto);
        max-width: inherit;
      }
    }

    li a {
      display: block;
      @include u-padding-y(1);
      @include u-font-size('body', '2xs');
      text-decoration: none;

      @include at-media('tablet-lg') {
        @include u-padding(1);
        margin-right: units(1);
      }
    }
  }
}

.chp-card {
  @include card();
  display: flex;
  width: 100%;
  margin: 0 0 units(2) 0;
  align-items: flex-start;
  text-decoration: none;
  @include u-radius('md');

  @include at-media(tablet) {
    margin: 0 0 units(2) 0;
    min-height: 195px;
  }

  &:hover {
    transform: translate3d(0px, -1px, 0px);
    box-shadow: rgba(0, 0, 0, 0.22) 0px 19px 43px, rgba(0, 0, 0, 0.18) 0px 4px 11px;
  }

  &__figure {
    flex-shrink: 0;
    align-items: center;
    padding-right: units(3);
  }

  &__figure svg,
  &__figure img {
    width: 8rem;
  }

  &__figure svg {
    fill: color('primary');
  }

  &__body {
    flex: 1;
    min-width: 0;

    p {
      color: color('ink');
    }
  }

  &__header {
    text-transform: uppercase;
    color: color('base-darker');
    @include u-font-size('body', 'md');
  }
}

.chp-card--wide {
  @include at-media(desktop) {
    width: 100%;
    margin: 0 0 units(2) 0;
    min-height: inherit;
  }
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
  padding: 0;
}

.chp-about-logos {
  clear: both;
  margin: units(4) 0;
  text-align: center;

  img {
    max-width: 200px;
    float: left;
    padding: units(2);

    @include at-media(desktop) {
      float: none;
      padding-left: 0;
      padding-right: 0;
    }
  }
}
